import React from "react";
import {Card, Col, Container, Image, Row} from "react-bootstrap";
import {Stylesheet} from "../interface/stylesheet";
import { motion } from "framer-motion";
import { Frame, Page } from "framer";
import {CertItem} from "./CertItem";

export class Certificate extends React.Component {
    render() {
        return (
            <div>
                <div style={Styles.pageStyleTwo}>
                    <Card style={{ width: '100vw', maxWidth: '720px', marginTop: '10vh', border: 'none', background: 'none'}}>
                        <Card.Body>
                            {this.renderCerts()}
                        </Card.Body>
                    </Card>
                </div>
            </div>

        )
    };


    private renderCerts = () => {
        const result = [];

        for (const certinfo of certURL) {
            result.push(<CertItem photoUrl={certinfo['imageURL']}
                                  certUrl={certinfo['certUrl']}
                                  name={certinfo['name']} />)
        }
        return result
    }
}

