import React from 'react';
import logo from './logo.svg';
import './App.css';
import {NavigationBar} from "./navigationBar/NavigationBar";
import {About} from "./about/About";
import {Skills} from "./skills/Skills";
import {Footer} from "./footer/Footer";
import {Certificate} from "./certificate/Certificate";

interface props {
}

interface state {
    isAboutSelected: boolean;
    isWorkSelected: boolean;
    isProjectSelected: boolean;
    isCertSelected: boolean;
}
class App extends React.Component<any, state> {
    constructor(prop: any) {
        super(prop);
        this.state =
            {
                isAboutSelected: true,
                isWorkSelected: false,
                isProjectSelected: false,
                isCertSelected: false
            }
    }

    private isAboutShowed = () => {
        return this.state.isAboutSelected ?
            <About
                relevantExperienceClicked={this.isWorkClicked}
                certClicked={this.isCertClicked}
            /> : null;
    }

    private isWorkShowed = () => {
        return this.state.isWorkSelected ? <Skills /> : null;
    }
    private isCertShowed = () => {
        return this.state.isCertSelected ? <Certificate /> : null;
    }

    private isAboutClicked = () => {
        this.setState({
            isAboutSelected: true,
            isWorkSelected: false,
            isProjectSelected: false,
            isCertSelected: false
        })
    }
    private isCertClicked = () => {
        this.setState({
            isAboutSelected: false,
            isWorkSelected: false,
            isProjectSelected: false,
            isCertSelected: true
        })
    }

    private isWorkClicked = () => {
        this.setState({
            isAboutSelected: false,
            isWorkSelected: true,
            isProjectSelected: false,
            isCertSelected: false
        })
    }

    render() {
        return (
            <div className="App">
                <NavigationBar
                    aboutClicked={this.isAboutClicked}
                    relevantExperienceClicked={this.isWorkClicked}
                    certClicked={this.isCertClicked}
                />
                {this.isAboutShowed()}
                {this.isWorkShowed()}
                {this.isCertShowed()}
                <Footer />
            </div>
        );
    }
}

export default App;
