import {motion} from "framer-motion";
import {Card, Image} from "react-bootstrap";
import React from "react";

interface props {
    photoUrl: string;
    certUrl: string;
    name: string;
}

export class CertItem extends React.Component<props>  {
    render() {
        return (
            <div>
                <motion.div
                    whileHover={{scale:1.1}}
                    whileTap={{scale:1}}
                    transition={{ ease: "easeOut", duration: 2 }}
                    style={{marginBottom: '10vh'}}
                >
                    <Image style={Styles.certificateStyle} src={this.props.photoUrl} />
                </motion.div>
                <motion.div
                    initial={{ x: 26* 1.2 }}
                    animate={{ x: 0 }}
                    transition={{ ease: "easeOut", duration: 2 }}
                    whileHover={{y: 26}}
                    style={{marginBottom: '10vh'}}
                >
                    <Card.Text>
                        <a style={{color: 'inherit'}} href={this.props.certUrl}>
                            {this.props.name}
                        </a>
                    </Card.Text>
                </motion.div>
            </div>
        )
    }
}
const Styles = {
    certificateStyle: {
        justifyContent: "center",
            alignContent: "center",
            alignSelf: 'center',
            width: '80vw',
            maxWidth: '720px'
    }
}